}

.seqview button {
    padding: 0px;
    margin: 5px;
    font-size: 18px;
    background-color: #384620;
    width: 30px;	
    height: 30px;
    color: #FFF;
    border: 2px solid #384620;
    text-align: center;
    border-radius: 100%;

}

.seqview button:hover {
    background-color: #2a3418;
    border: 2px solid #2a3418;
    
}

.seqview button:active {
    background-color: black;
    border: 2px solid black;
  /*  border-radius: 100%; */
}

.seqview button:focus {
    outline: 0;
}

